
.pure-button {
  transition: all 0.3s ease;
  border-radius: 4px;
  background-color: transparent;
  border: #29b474 1px solid;
  color: #29b474;
  display: inline-block;
  font-family: inherit;
  font-size: 100%;
  outline: 0;
  padding: 0.5em 1em;
  text-align: center;
  text-decoration: none;
}
.pure-button:hover {
  background-color: #29b474;
  color: #fff;
  cursor: pointer;
}
.pure-button.full-width {
  display: block;
  margin-bottom: 10px;
}
.pure-button.pure-button-primary {
  background-color: #29b474;
  color: #fff;
}
.pure-button.pure-button-primary:hover {
  background-color: #25a268;
}
.pure-button.pure-button-white {
  background-color: transparent;
  border-color: #fff;
  color: #fff;
}
.pure-button.pure-button-white:hover {
  background-color: rgba(0,0,0,0.1);
}
.pure-button.pure-button-success {
  background-color: #29b474;
  color: #fff;
}
.pure-button.pure-button-success:hover {
  background-color: #25a268;
}
.pure-button.pure-button-twitter {
  border-radius: 25px;
  background-color: #4099ff;
  border: none;
  color: #fff;
  line-height: 50px;
  padding: 0 30px;
}
.pure-button.pure-button-twitter:hover {
  background-color: #2088ff;
}
.pure-button.pure-button-subtle {
  background-color: transparent;
  border-color: #ccc;
  color: #666;
}
.pure-button.pure-button-subtle:hover {
  background-color: #e1e1e1;
  color: #444;
}
.pure-button.pure-button-text {
  border-color: transparent;
}
.pure-button.pure-button-homework {
  background-color: transparent;
  border-color: #fd3c51;
  color: #fd3c51;
}
.pure-button.pure-button-homework:hover {
  background-color: #fd3c51;
  color: #fff;
}
html,
body {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.hidden {
  display: none;
}
.pull-left {
  float: left;
}
.pull-right {
  float: right;
}
.clearfix {
  clear: both;
  float: none;
}
.fa-icon {
  width: auto;
  height: 1em;
}
.no-margin {
  margin: 0 !important;
}
.no-padding {
  padding: 0 !important;
}
.background-white {
  background-color: #fff !important;
}
.text-white {
  color: #fff;
}
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.2s;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}
.fade-enter-to,
.fade-leave {
  opacity: 1;
}
.main-container {
  border-radius: 4px;
  position: relative;
}
@media (max-width: 800px) {
.main-container {
    border-radius: 0;
}
}
.main-container.main-container-padded {
  padding: 20px;
}
.content-block {
  border-radius: 4px;
  padding: 20px;
  margin: 20px 0 0 0;
}
@media (max-width: 800px) {
.content-block {
    border-radius: 0;
}
}
.content-block.white-block {
  background-color: #fff;
}
.icon-margin {
  margin: 0 5px;
}
html {
  background-color: #e9e9e9;
  transition: background-color 0.6s;
}
html::-webkit-scrollbar {
  display: none;
}
html.colourful {
  background-color: #29b474;
}
html.dark-mode {
  background-color: #242424;
}
html,
body {
  margin: 0;
  padding: 0;
}
body.disable-scroll {
  overflow: hidden;
  min-height: 100%;
  max-height: 100vh;
  padding-bottom: 1px;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
.main-page .col {
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  padding: 0;
/*top 60px*/
}
.main-page .col .container {
  padding: 20px;
}
.main-page .col .container h1 {
  margin: 0;
  padding: 0;
  color: #444;
  margin-bottom: 5px;
}
.main-page .col#col-main {
  border-radius: 4px;
  margin: 0 auto 60px auto;
  max-width: 780px;
  padding-top: 80px;
  position: relative;
}
.main-page .col#col-main.narrow {
  max-width: 640px;
}
@media (max-width: 800px) {
.main-page .col#col-main {
    max-width: 100%;
    margin: 0 0px;
}
}
/* App states */
#app.authenticating .main-page .col {
  top: 0;
}
#content-overlay {
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(0,0,0,0);
  pointer-events: none;
  position: fixed;
  transition: background-color 0.3s;
  z-index: 55;
}
#content-overlay.visible {
  background-color: rgba(0,0,0,0.8);
  pointer-events: all;
}
.page-header {
  transition: all 0.2s ease;
  background-color: #29b474;
  height: 160px;
  position: absolute;
  left: 0;
  right: 0;
  top: 60px;
  text-align: center;
  z-index: -1;
}
@media (max-width: 800px) {
.page-header {
    height: 84px;
}
}
.page-header.minimized {
  height: 0px;
}
#app.chat .page-header {
  background-color: #fd3c51;
}
#app.admin .page-header {
  background-color: #161616;
}
#app.fourcorners .page-header {
  background-color: #161616;
}
html.colourful .page-header {
  display: none;
}


